@import '../main-block';
.main { 
  .block_all_items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 50px;
    grid-row-gap: 20px;
  }
  .block_item {
    width: 100%;
    text-align: left;

  }
  span {
    font-size: 26px;
    font-weight: 700;
  }
  P {    
    width: 100%;
    // margin: 10px 0;
    text-align: center;   
    border-radius: 20px;
    border: 1px solid #ccc;
    padding: 40px 0;   
    font-size: 25px;
    font-weight: 700;
    cursor: pointer;
    background-color: #ddd;
    &:hover {
      box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    }
  }
}
