.footer {
  text-align: left;
  padding: 5vh 20px;
  margin: 0;
  .link_to_main,
  .link_to_login {
    background-color: yellowgreen;
    font-size: 18px;
    padding: 20px;
    border-radius: 10px;
    text-decoration: none;
  }
  .link_to_login {
    background-color: rgb(129, 17, 204);
    float: right;
    margin-left: 30px;
    padding: 10px;
    color: #fff;
  }
  .name_member {
    float: right;
    font-size: 25px;
    line-height: 45px;
  }
}
