.main {
  height: 100vh;
  font-family: 'PT Sans', sans-serif;
  box-sizing: border-box;
  padding-top: 30vh;
  .login_block {
    position: relative;
    width: 350px;   
    border-radius: 10px;
    padding: 30px;
    margin: 0 auto;
    box-shadow: 0px 4px 41px 0px rgba(34, 60, 80, 0.2);
    label {
      font-size: 18px;
      margin: 15px 0 5px;
      font-weight: 700;
    }
    h2 {
      text-align: center;
      font-size: 30px;
    }
    .login_button {
      font-size: 20px;
      margin-top: 30px;
    }
    a.login_button_guest {
      font-size: 20px;
      text-decoration: none;
      display: block;
      text-align: center;
      margin-top: 20px;
     
    }
    .loading {
      text-align: center;
      line-height: 30vh;
    }
  }
}
