.main {
  width: 15%;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  box-sizing: border-box;
  margin: 20px;
  height: 75vh;
  border-radius: 10px;
  background-color: #fff;
  display: inline-block;
  .navLink {
    font-family: 'Open Sans', sans-serif;
    text-decoration: none;
    line-height: 100px;
    padding-left: 25px;
    display: block;
    border-bottom: 1px sold slategrey;
    font-size: 20px;
    text-align: left;
    color: rgb(209, 183, 183);
    &:hover {
      background-color: rgb(196, 207, 214);
      color: rgb(10, 0, 0);
    }
  }
}
